@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:ital@1&family=Poppins:wght@500&display=swap');

*{
    box-sizing: border-box;
    padding: 0%;
    margin: 0%;
}
.profile{
    display: flex;
    flex-direction: row;
    background: linear-gradient(to right, yellow,white);
}
#propic{
    height: 350px;
    width: 350;
    overflow: hidden;
}
p{
    font-size: 1.5rem;
    font-family: 'Poppins', sans-serif;
}
.icons{
    display: flex;
    flex-direction: column;
    column-gap: 20px;
    align-items: center;
    justify-content: center;
    background: linear-gradient(yellow,white);
}
#tech{
    display: flex;
    column-gap: 20px;
    align-items: center;
    justify-content: center;
}
.icon1{
    zoom: 1000%;
}
.icon2{
    zoom: 1000%;
}
.icon3{
    zoom: 1000%;
}
.icon4{
    zoom: 1000%;
}
.icon5{
    zoom: 1000%;
}
.icon6{
    zoom: 1000%;
}
.footer{
    height: 50px;
    background: linear-gradient(to top,yellow,white);
}
iframe{
    height: auto;
    max-width: 100%;
    aspect-ratio: 16/9;
}
@media only screen and (max-width:576px){
    .profile{
        flex-direction: column;
    }
    .icons{
        flex-direction: column;
    }
    .flexicon{
        width: 100%;
    }
.hr{
    color: rgb(rgb(255, 255, 255), rgb(0, 0, 0), rgb(131, 131, 131));
}
span{
    color: red;
}
.why{
    height: 100px;
}
.p2{
    width: 50%;
    word-spacing: 50px;
}
.video{
    width: 50%;
    padding: 0%;
}
.iframe{
    display: flex;
    flex-direction: row;
}
}
@media only screen and (max-width:776px){
    .profile{
        flex-direction: column;
    }
    .icons{
        flex-direction: column;
    }
    .iframe{
        flex-direction: column;
    }
}
@media only screen and (max-width:976px){
    .profile{
        flex-direction: column;
    }
    .icons{
        flex-direction: column;
    }
    .iframe{
        flex-direction: column;
    }
}