*body{
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}

h1{
    color: rgb(0, 86, 247);
}
.entire{
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: linear-gradient(to right, yellow,white);
    box-sizing: border-box;
}
label{
    color: red;
}
input[type='text']{
    width: 50vw;
    padding: 20px;
    margin: 5px;
}
input[type='number']{
    width: 50vw;
    padding: 20px;
    margin: 5px;
}
input[type='email']{
    width: 50vw;
    padding: 20px;
    margin: 5px;
}
input[type='password']{
    width: 50vw;
    padding: 20px;
    margin: 5px;
}
button{
    cursor: pointer;
    width: 100px;
    height: 40px;
    background: linear-gradient(yellow,white);
    color: black;
    font-weight: bolder;
    font-family:'Times New Roman', Times, serif;
}
.checkbox{
    display: flex;
    margin: 10px;
    justify-content: center;
    align-items: center;
}
span{
    color: rgb(0, 86, 247);
    font-weight: bolder;
}
.icons{
    display: flex;
    flex-direction: row;
    gap: 5px;
    margin: 10px;
    padding: 10px;
}